@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300;700&family=Merriweather:wght@300;700&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    text-align: center;
    color: white;
    background-color: #295488;
    font-family: 'Merriweather Sans', sans-serif;
    font-size: 18px;
}

#score-board {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-weight: 700;
    width: 100vw;
    font-family: 'Merriweather', serif;
    font-size: 18px;
}

@media (max-height: 700px) and (max-width: 450px) {
    #score-board {
        font-size: 14px;
    }
}

#status {
    padding: 10px;
    font-size: 16px;
}

.red {
    color: #ff8383;
}

.green {
    color: #94f96c;
}

@media (max-height: 700px) and (max-width: 450px) {
    #status {
        font-size: 12px;
    }
}

.card {
    max-height: 300px;
    padding: 5px;
    font-size: 15px;
    background-color: #ffffff;
    color: #050708;
    box-shadow: 0 0 5px #00000050;
    border-radius: 15px;
    font-family: 'Merriweather', serif;
    font-weight: 700;
    text-shadow: 0 2px 2px #f7a01c;
    border: solid 3px #f7a01c;
    transition: 300ms ease-in-out box-shadow, 300ms ease-in-out color;
}

.card img {
    width: 132px;
}

.card:hover {
    cursor: pointer;
    box-shadow: 0 0 8px #000000;
    color: #1a58a5;
    text-shadow: 0 1px 1px #f7a01c;
}

.click-card {
    -webkit-animation: click-card 400ms ease-out;
            animation: click-card 400ms ease-out;
}

@-webkit-keyframes click-card {
    from {
        box-shadow: inset #f7a01c 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        box-shadow: inset #f7a01c 0 0 40px;
        -webkit-transform: scale(0.98);
                transform: scale(0.98);
    }

    to {
        box-shadow: inset #f7a01c 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes click-card {
    from {
        box-shadow: inset #f7a01c 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        box-shadow: inset #f7a01c 0 0 40px;
        -webkit-transform: scale(0.98);
                transform: scale(0.98);
    }

    to {
        box-shadow: inset #f7a01c 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@media (min-width: 800px) {
    .card img {
        width: 180px;
    }
}

@media (max-height: 700px) and (max-width: 450px) {
    .card {
        font-size: 13px;
    }
    .card img {
        width: 80px;
    }
}

#cards-board {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    width: 95vw;
    height: 95%;
}

.fade-in {
    -webkit-animation: fade-in 1s ease-in;
            animation: fade-in 1s ease-in;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0px);
                filter: blur(0px);
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0px);
                filter: blur(0px);
    }
}

@media (min-width: 1200px) {
    #cards-board {
        width: 80vw;
    }
}

@media (min-width: 800px) {
    #cards-board {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

main {
    display: grid;
    grid-template-rows: 15% 5% 10% 70%;
    place-items: center;
    height: 100vh;
}

h1 {
    color: #f9c16c;
    text-shadow: 0 0 8px #00000050;
    font-size: 30px;
    line-height: 20px;
}

#degrassi-logo {
    width: 230px;
    display: block;
    margin: 0 auto;
    -webkit-filter: drop-shadow(0 0 8px #00000050);
            filter: drop-shadow(0 0 8px #00000050);
}

#restart-btn {
    position: absolute;
    top: 10px;
    left: 5px;
    background-color: #f9c16c;
    color: #050708;
    padding: 6px;
    font-size: 12px;
    font-family: 'Merriweather Sans', sans-serif;
    border: none;
    border-radius: 10px;
    transition: 300ms ease-in-out box-shadow, 300ms ease-in-out background-color;
}

#restart-btn:hover {
    cursor: pointer;
    box-shadow: 0 0 8px #00000050;
    background-color: #ff8383;
}

@media (max-height: 700px) and (max-width: 450px) {
    h1 {
        font-size: 20px;
        line-height: 15px;
    }
    #degrassi-logo {
        width: 200px;
    }
}

.series-btn-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
}

.series-btn-container label {
    margin-top: 10px;
    font-size: 13px;
}

.series-button {
    background-color: #5e7ca2;
    color: #ffffff;
    padding: 10px;
    width: 80px;
    font-size: 16px;
    font-family: 'Merriweather Sans', sans-serif;
    font-weight: 300;
    border: none;
    border-radius: 10px;
    transition: 300ms ease-in-out box-shadow, 300ms ease-in-out background-color;
    height: 50px;
}

.series-button.active {
    border: 2px solid #f7a01c;
    background-color: #1a58a5;
    font-weight: 700;
}

.series-button:hover {
    cursor: pointer;
    box-shadow: 0 0 8px #00000050;
}

#new-game-btn {
    background-color: #1a58a5;
    margin-bottom: 100px;
    color: #ffffff;
    padding: 30px;
    font-size: 18px;
    font-family: 'Merriweather Sans', sans-serif;
    font-weight: 700;
    border: 3px solid #f7a01c;
    border-radius: 50px;
    transition: 300ms ease-in-out box-shadow;
}

#new-game-btn:hover {
    cursor: pointer;
    box-shadow: 0 0 8px #00000050;
}

#series-select {
    font-size: 16px;
}

#series-container {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.click-button {
    -webkit-animation: click-button 500ms ease-out;
            animation: click-button 500ms ease-out;
}

@-webkit-keyframes click-button {
    from {
        box-shadow: inset #00000050 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        box-shadow: inset #00000050 0 0 20px;
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }

    to {
        box-shadow: inset #00000050 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes click-button {
    from {
        box-shadow: inset #00000050 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        box-shadow: inset #00000050 0 0 20px;
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }

    to {
        box-shadow: inset #00000050 0 0 0;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
